.header-container .navbar {
  padding: 0;
}
.header-container .navbar .container-fluid {
  padding: 1rem;
  background-color: var(--gray-bg);
  border-bottom: 1px solid var(--gray-border-line);
}
.header-container .navbar .container-fluid .navbar-brand img {
  width: 5.5rem;
}
.header-container .navbar ul {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.header-container .navbar ul li {
  width: 100%;
}
.header-container .navbar ul li a {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 100%;
  height: 4.5rem;
  position: relative;
  color: var(--gray-title);
  line-height: 4.5rem;
  font-weight: 700;
  transition: 0.2s;
}
.header-container .navbar ul li a:hover {
  color: var(--red);
}
.header-container .navbar ul li a.active {
  color: var(--red);
}
.header-container .navbar ul li a.active::after {
  content: "";
  height: 5px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background: var(--red);
}
.header-container .navbar-toggler {
  border: none;
  transition: 0.2s;
}
.header-container .navbar-toggler:hover {
  background: var(--gray-200);
}
.header-container .navbar-toggler:focus {
  box-shadow: none;
}

.modal {
  max-width: 270px;
}
.modal .modal-content {
  max-width: 270px;
}
.modal .modal-content .modal-body,
.modal .modal-content .list-group-item {
  padding: 0;
}
.modal .modal-content .list-group-item {
  border: none;
  padding: 1rem 2rem;
}
.modal .modal-content .list-group-item:first-child {
  background-color: var(--gray-bg);
}
.modal .modal-content .list-group-item a {
  display: grid;
  grid-template-columns: 1fr 5fr;
  align-items: center;
  padding: 0.5rem 0;
  color: var(--gray-200);
  width: 100%;
  font-weight: 700;
  text-align: left;
  transition: 0.2s;
}
.modal .modal-content .list-group-item a:hover {
  color: var(--red);
}
.modal .modal-content .list-group-item a:active {
  color: var(--red);
}
.modal .modal-content .list-group-item a i {
  font-size: 1.4rem;
}

/* main */
main {
  padding: 2rem 0;
}
main .banner-carousel {
  position: relative;
}
main .banner-carousel .images {
  display: flex;
}
main .banner-carousel .images .slick-slide {
  margin: 1rem;
}
main .banner-carousel .images .slick-center {
  transform: scale(1.2);
}
main .sliders {
  opacity: 0.7;
}
main .sliders #arrow-prev,
main .sliders #arrow-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2rem;
}
main .sliders #arrow-prev {
  left: 1rem;
}
main .sliders #arrow-next {
  right: 1rem;
}